.component {
    background-color: white;
    width: 23%;
    max-width: 600px;
    margin: 1em auto;
    padding-bottom: 2em;
    border: 0.1px solid black;
    border-radius: 10px;
}

.musicCover {
    border-radius: 10%;
}

.playButton {
    background: none;
    border: none;
    align-items: center;
    justify-content: center;
}

.subTitle {
    margin-top: -1em;
    color: #4f4f4f;
}

.time {
    margin: 0 auto;
    width: 80%;
    display: flex;
    justify-content: space-between;
    color: #828282;
    font-size: smaller;
}

.timeline {
    width: 80%;
    background-color: #27ae60;
}
